<template >
  <div>
    <h2>사용자 게시판</h2>
    <div
      class="container"
      v-for="(value, index) in data"
      :key="index"
      @click="detail(index)"
    >
        <!-- <div class="img_wrap">
            <img :src="value.image" alt="">
        </div> -->
        <div>

      <div class="f_wrap">
        <div class="title">{{ value.title }}</div>
        <div class="name">{{ value.writer }}</div>
      </div>
      <div class="content">{{ value.content }}</div>
    </div>
        </div>
    <button @click="write">글쓰기</button>
  </div>
</template>
<script>
import data from "@/data";
export default {
  data() {
    return {
      data: data,
    };
  },
  methods: {
    write() {
      this.$router.push({
        path: "create",
      });
    },
    detail(index) {
      this.$router.push({
        name: "Detail",
        params: {
          contentId: index,
        },
      });
    },
  },
};
</script>
<style scoped>
.container{
}
.img_wrap {
    width: 100px;
    
}
.img_wrap img{
    width:100%;
    text-align: center;
}
</style>


