<template>
  <div>
    <h2>상세페이지</h2>
    <div class="container">
      <div class="f_wrap">
        <div class="title">{{ data.title }}</div>
        <div class="writer">{{ data.writer }}</div>
      </div>
      <div class="content">
        <div>

        {{ data.content }}
        </div>
        <img :src="data.image" alt="">
        </div>
    </div>
    <button @click="updateData">수정</button>
    <button @click="deleteData">삭제</button>
  </div>
</template>

<script>
import data from "@/data";
export default {
  name: "Detail",
  data() {
    const index = this.$route.params.contentId;
    return {
      data: data[index],
      index: index,
    };
  },

  methods: {
    updateData() {
      this.$router.push({
        name: "Create",
        params: {
          contentId: this.index,
        },
      });
    },
    deleteData() {
      data.splice(this.index, 1);
      this.$router.push({
        path: "/",
      });
    },
  },
};
</script>

<style scoped>
img {
    max-width: 40%;
}

</style>


